import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'HomePage',
    component: () => import('../views/HomePage'),
    meta: {
      isLogin: true,
      title: "主页"
    },
    children: [

      // 郭恒玉
      {
        path: '/',
        redirect: '/adServing'
      },
      {//新分享商城商品
        path: '/newshare',
        name: 'Newshare',
        component: () => import('../views/guohengyu/Newshare'),
        meta: {
          isLogin: true,
          title: "新分享商城商品"
        }
      },
      {//拍吧商品
        path: '/shootIt',
        name: 'ShootIt',
        component: () => import('../views/guohengyu/ShootIt'),
        meta: {
          isLogin: true,
          title: "拍吧商品"
        }
      },
      {//平台快购商品
        path: '/quickBuy',
        name: 'QuickBuy',
        component: () => import('../views/guohengyu/QuickBuy'),
        meta: {
          isLogin: true,
          title: "平台快购商品"
        }
      },
      {//平台网购商品
        path: '/onlineShopping',
        name: 'OnlineShopping',
        component: () => import('../views/guohengyu/OnlineShopping'),
        meta: {
          isLogin: true,
          title: "平台网购商品"
        }
      },
      {//券式团购商品
        path: '/couponGroupPurchase',
        name: 'CouponGroupPurchase',
        component: () => import('../views/guohengyu/CouponGroupPurchase'),
        meta: {
          isLogin: true,
          title: "券式团购商品"
        }
      },
      {//聚宝盆
        path: '/cornucopia',
        name: 'Cornucopia',
        component: () => import('../views/guohengyu/Cornucopia'),
        meta: {
          isLogin: true,
          title: "聚宝盆"
        }
      },
      {//平台品牌管理
        path: '/platformBrandManage',
        name: 'PlatformBrandManage',
        component: () => import('../views/guohengyu/PlatformBrandManage'),
        meta: {
          isLogin: true,
          title: "平台品牌管理"
        }
      },
      {//分类管理
        path: '/classifyManage',
        name: 'ClassifyMange',
        component: () => import('../views/guohengyu/ClassifyManage'),
        meta: {
          isLogin: true,
          title: "分类管理"
        }
      },
      {//平台推送
        path: '/platformpush',
        name: 'Platformpush',
        component: () => import('../views/guohengyu/Platformpush'),
        meta: {
          isLogin: true,
          title: "平台推送"
        }
      },
      {//广告投放
        path: '/adServing',
        name: 'AdServing',
        component: () => import('../views/guohengyu/AdServing'),
        meta: {
          isLogin: true,
          title: "广告投放"
        }
      },

    ]

  },
  {
    path: '/Login',
    name: 'Login',
    component: () => import('../views/Login'),
    meta: {
      isLogin: false,
      title: "登录"
    }
  }

]

const router = new VueRouter({
  routes
})

export default router
